import Template from './template'

export const PREFIX = '\n𝗕𝗹𝗲𝗽 '

// not sure why this needs async, but it does...
export default async function (PineJS) {
	return [
		Template({
			name: 'BinanceOI',
			description: 'Open Interest',
			plotNames: ['!binance_BTCUSDT_openinterest', '!binance_ETHUSDT_asdf'],
		})(PineJS),
		Template({
			name: 'EthVolume',
			description: 'EthVolume',
			plotNames: ['blockchain_eth_totalamount_1m'],
		})(PineJS),
		Template({
			name: 'EthTx',
			description: 'EthTx',
			plotNames: ['blockchain_eth_totaltx_1m'],
		})(PineJS),
		Template({
			name: 'EthUniqueSenders',
			description: 'EthUniqueSenders',
			plotNames: ['blockchain_eth_uniquefroms_1m'],
		})(PineJS),
		Template({
			name: 'EthUniqueRecipients',
			description: 'EthUniqueRecipients',
			plotNames: ['blockchain_eth_uniquerecipients_1m'],
		})(PineJS),
		Template({
			name: 'EthAvgDiff',
			description: 'EthBlockAverageDifficulty',
			plotNames: ['blockchain_eth_averagedifficulty_1m'],
		})(PineJS),
		Template({
			name: 'EthAvgGas',
			description: 'EthBlockAverageGasUsed',
			plotNames: ['blockchain_eth_averagegasused_1m'],
		})(PineJS),
		Template({
			name: 'BinanceBTCUSDTOpenInterest',
			description: 'Binance BTCUSDT Open Interest',
			plotNames: ['binance_BTCUSDT_sumopeninterest_5m'],
		})(PineJS),
		Template({
			name: 'BinanceBTCUSDTOpenInterestValue',
			description: 'Binance BTCUSDT Open Interest Value',
			plotNames: ['binance_BTCUSDT_sumopeninterestvalue_5m'],
		})(PineJS),
	].map(load)
}

// prefix descriptions with \nBlep to show at beginning of TV indicator list.
function load (indicator) {
	indicator.metainfo.description = PREFIX + indicator.metainfo.description
	return indicator
}

